'use client'

import { useState, useEffect } from 'react'
import { useSearchParams } from 'next/navigation'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Progress } from '@/components/ui/progress'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Skeleton } from '@/components/ui/skeleton'
import { 
  Search, 
  Download, 
  Filter
} from 'lucide-react'
import Image from 'next/image'
import Link from 'next/link'
import { io, Socket } from 'socket.io-client'
import { ThemeToggle } from '@/components/ThemeToggle'
import { useTheme } from '@/contexts/ThemeContext'

interface Player {
  name: string
  tag: string
  soloq: string | null
  flexq: string | null
  global_kda: number
  global_kill: number
  global_death: number
  global_assists: number
  global_kp: number
  global_winrate: number
  nb_game: number
  nb_win: number
  nb_lose: number
  score_moyen: number
  role: string[]
}

interface Champion {
  nom: string
  nombre_de_parties: number
  nombre_win: number
  nombre_lose: number
  winrate: number
  kill: number
  death: number
  assit: number
  dangerousness: number
  kda: number
  kill_participation: number
}

interface FilterOptions {
  role: string[]
  champion: string[]
  match_types: string[]
  start_date: string
  end_date: string
}

export default function PlayerPage() {
  const searchParams = useSearchParams()
  const username = searchParams.get('username') || ''
  const { theme } = useTheme()
  
  const [player, setPlayer] = useState<Player | null>(null)
  const [champions, setChampions] = useState<Champion[]>([])
  const [allChampions, setAllChampions] = useState<any[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [socket, setSocket] = useState<Socket | null>(null)
  const [downloadProgress, setDownloadProgress] = useState(0)
  const [isDownloading, setIsDownloading] = useState(false)
  const [downloadGames, setDownloadGames] = useState(1)
  
  const [filters, setFilters] = useState<FilterOptions>({
    role: ['all'],
    champion: ['all'],
    match_types: ['all'],
    start_date: '',
    end_date: ''
  })

  // Initialisation du socket
  useEffect(() => {
    const newSocket = io('http://localhost:5001')
    setSocket(newSocket)

    newSocket.on('connect', () => {
      console.log('Connected to server')
    })

    newSocket.on('progress', (data) => {
      setDownloadProgress(data.progress)
    })

    newSocket.on('download_complete', (data) => {
      setIsDownloading(false)
      setDownloadProgress(0)
      fetchPlayerData()
    })

    newSocket.on('download_error', (data) => {
      setIsDownloading(false)
      setDownloadProgress(0)
      setError(data.error)
    })

    return () => {
      newSocket.close()
    }
  }, [])

  // Chargement des données du joueur
  const fetchPlayerData = async () => {
    if (!username) return
    
    try {
      setLoading(true)
      setError(null)
      
      const response = await fetch('http://localhost:5001/api/search', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ username }),
      })
      
      if (!response.ok) {
        const errorData = await response.json()
        throw new Error(errorData.error || 'Failed to fetch player data')
      }
      
      const data = await response.json()
      setPlayer(data.player)
      setChampions(data.champions)
      setAllChampions(data.all_champions)
    } catch (err) {
      setError(err instanceof Error ? err.message : 'An error occurred')
    } finally {
      setLoading(false)
    }
  }

  // Filtrage des données
  const applyFilters = async () => {
    if (!username) return
    
    try {
      setLoading(true)
      setError(null)
      
      const response = await fetch('http://localhost:5001/api/filter', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          username,
          ...filters
        }),
      })
      
      if (!response.ok) {
        const errorData = await response.json()
        throw new Error(errorData.error || 'Failed to filter data')
      }
      
      const data = await response.json()
      setPlayer(data.player)
      setChampions(data.champions)
    } catch (err) {
      setError(err instanceof Error ? err.message : 'An error occurred')
    } finally {
      setLoading(false)
    }
  }

  // Téléchargement de nouveaux jeux
  const handleDownload = async () => {
    if (!username || !socket) return
    
    try {
      setIsDownloading(true)
      setDownloadProgress(0)
      
      const response = await fetch('http://localhost:5001/api/download', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          username,
          nb_games: downloadGames,
          session_id: socket.id
        }),
      })
      
      if (!response.ok) {
        const errorData = await response.json()
        throw new Error(errorData.error || 'Failed to start download')
      }
      
      socket.emit('join', { session_id: socket.id })
    } catch (err) {
      setIsDownloading(false)
      setError(err instanceof Error ? err.message : 'An error occurred')
    }
  }

  useEffect(() => {
    fetchPlayerData()
  }, [username])

  if (loading && !player) {
    return <LoadingSkeleton />
  }

  if (error) {
    return <ErrorPage error={error} />
  }

  if (!player) {
    return <ErrorPage error="Player not found" />
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-slate-100">
      {/* Navigation */}
      <nav className="bg-white/80 backdrop-blur-md border-b border-slate-200 sticky top-0 z-50">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between h-16">
            <div className="flex items-center space-x-8">
              <Link href="/" className="flex items-center space-x-3">
                <Image
                  src="/images/logo.png"
                  alt="ZhonyaS Logo"
                  width={32}
                  height={32}
                  className="rounded-lg"
                />
                <span className="text-xl font-bold text-slate-900">ZhonyaS</span>
              </Link>
            </div>
            
            <div className="flex items-center space-x-3">
              <div className="relative">
                <Input
                  type="text"
                  placeholder="Search player..."
                  defaultValue={username}
                  className="w-80 bg-white border-slate-300 text-slate-900 placeholder:text-slate-500 focus:border-blue-500 focus:ring-blue-500"
                  onKeyPress={(e) => {
                    if (e.key === 'Enter') {
                      window.location.href = `/player?username=${encodeURIComponent((e.target as HTMLInputElement).value)}`
                    }
                  }}
                />
                <Search className="absolute right-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-slate-400" />
              </div>
              <Button 
                onClick={() => {
                  const input = document.querySelector('input[placeholder="Search player..."]') as HTMLInputElement
                  if (input.value) {
                    window.location.href = `/player?username=${encodeURIComponent(input.value)}`
                  }
                }}
                className="bg-blue-600 hover:bg-blue-700 text-white"
              >
                <Search className="w-4 h-4" />
              </Button>
            </div>
          </div>
        </div>
      </nav>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Player Header */}
        <div className="mb-8">
          <div className="bg-white rounded-2xl shadow-lg p-8 mb-6">
            <h1 className="text-4xl font-bold text-slate-900 mb-6">
              {player.name}#{player.tag}
            </h1>
            
            {/* Player Stats */}
            <div className="grid grid-cols-2 md:grid-cols-5 gap-6 mb-8">
              <div className="text-center">
                <div className="text-3xl font-bold text-slate-900 mb-1">{player.nb_game}</div>
                <div className="text-sm text-slate-600 font-medium">Total Games</div>
              </div>
              
              <div className="text-center">
                <div className="text-3xl font-bold text-slate-900 mb-1">{player.global_winrate}%</div>
                <div className="text-sm text-slate-600 font-medium">Winrate</div>
              </div>
              
              <div className="text-center">
                <div className="text-3xl font-bold text-slate-900 mb-1">{player.global_kda}</div>
                <div className="text-sm text-slate-600 font-medium">KDA</div>
              </div>
              
              <div className="text-center">
                <div className="text-3xl font-bold text-slate-900 mb-1">{player.global_kp}%</div>
                <div className="text-sm text-slate-600 font-medium">KP</div>
              </div>
              
              <div className="text-center">
                <div className="text-3xl font-bold text-slate-900 mb-1">{player.score_moyen}</div>
                <div className="text-sm text-slate-600 font-medium">Score</div>
              </div>
            </div>

            {/* Rank Display */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <RankCard title="Ranked Solo" rank={player.soloq} />
              <RankCard title="Ranked Flex" rank={player.flexq} />
            </div>
          </div>
        </div>

        {/* Filters and Download */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 mb-8">
          {/* Filters */}
          <Card className="bg-white border-slate-200 shadow-lg">
            <CardHeader>
              <CardTitle className="text-slate-900 flex items-center space-x-2">
                <Filter className="w-5 h-5" />
                <span>Filters</span>
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              {/* Role Selection */}
              <div>
                <label className="text-sm font-medium text-slate-700 mb-2 block">Role</label>
                <div className="flex flex-wrap gap-2">
                  {['all', 'TOP', 'JUNGLE', 'MIDDLE', 'BOTTOM', 'UTILITY'].map((role) => (
                    <Button
                      key={role}
                      variant={filters.role.includes(role) ? 'default' : 'outline'}
                      size="sm"
                      onClick={() => {
                        if (role === 'all') {
                          setFilters({ ...filters, role: ['all'] })
                        } else {
                          const newRoles = filters.role.includes(role)
                            ? filters.role.filter(r => r !== role)
                            : [...filters.role.filter(r => r !== 'all'), role]
                          setFilters({ ...filters, role: newRoles.length === 0 ? ['all'] : newRoles })
                        }
                      }}
                      className={filters.role.includes(role) ? 'bg-blue-600' : 'bg-white border-slate-300 text-slate-700 hover:bg-slate-50'}
                    >
                      {role}
                    </Button>
                  ))}
                </div>
              </div>

              {/* Queue Type */}
              <div>
                <label className="text-sm font-medium text-slate-700 mb-2 block">Queue Type</label>
                <Select
                  value={filters.match_types[0] || 'all'}
                  onValueChange={(value) => setFilters({ ...filters, match_types: [value] })}
                >
                  <SelectTrigger className="bg-white border-slate-300 text-slate-900">
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">All Queues</SelectItem>
                    <SelectItem value="soloq">SoloQ</SelectItem>
                    <SelectItem value="flex">Flex</SelectItem>
                    <SelectItem value="normal">Normal</SelectItem>
                    <SelectItem value="tourney">Tournament</SelectItem>
                  </SelectContent>
                </Select>
              </div>

              {/* Champion Selection */}
              <div>
                <label className="text-sm font-medium text-slate-700 mb-2 block">Champion</label>
                <Select
                  value={filters.champion[0] || 'all'}
                  onValueChange={(value) => setFilters({ ...filters, champion: [value] })}
                >
                  <SelectTrigger className="bg-white border-slate-300 text-slate-900">
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">All Champions</SelectItem>
                    {allChampions.map((champ) => (
                      <SelectItem key={champ.name} value={champ.name}>
                        {champ.name}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>

              {/* Date Range */}
              <div className="grid grid-cols-2 gap-2">
                <div>
                  <label className="text-sm font-medium text-slate-700 mb-2 block">Start Date</label>
                  <Input
                    type="date"
                    value={filters.start_date}
                    onChange={(e) => setFilters({ ...filters, start_date: e.target.value })}
                    className="bg-white border-slate-300 text-slate-900"
                    min="2021-06-16"
                  />
                </div>
                <div>
                  <label className="text-sm font-medium text-slate-700 mb-2 block">End Date</label>
                  <Input
                    type="date"
                    value={filters.end_date}
                    onChange={(e) => setFilters({ ...filters, end_date: e.target.value })}
                    className="bg-white border-slate-300 text-slate-900"
                    min="2021-06-16"
                  />
                </div>
              </div>

              <Button 
                onClick={applyFilters} 
                disabled={loading}
                className="w-full bg-blue-600 hover:bg-blue-700 text-white"
              >
                {loading ? 'Applying...' : 'Apply Filters'}
              </Button>
            </CardContent>
          </Card>

          {/* Download */}
          <Card className="bg-white border-slate-200 shadow-lg">
            <CardHeader>
              <CardTitle className="text-slate-900 flex items-center space-x-2">
                <Download className="w-5 h-5" />
                <span>Download Games</span>
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div>
                <label className="text-sm font-medium text-slate-700 mb-2 block">
                  Number of games to download (1-10000)
                </label>
                <Input
                  type="number"
                  min="1"
                  max="10000"
                  value={downloadGames}
                  onChange={(e) => setDownloadGames(parseInt(e.target.value) || 1)}
                  className="bg-white border-slate-300 text-slate-900"
                />
              </div>

              {isDownloading && (
                <div className="space-y-2">
                  <div className="flex items-center justify-between text-sm text-slate-700">
                    <span>Downloading...</span>
                    <span>{downloadProgress}%</span>
                  </div>
                  <Progress value={downloadProgress} className="h-2" />
                </div>
              )}

              <Button 
                onClick={handleDownload} 
                disabled={isDownloading}
                className="w-full bg-green-600 hover:bg-green-700 text-white"
              >
                {isDownloading ? 'Downloading...' : 'Download Games'}
              </Button>
            </CardContent>
          </Card>
        </div>

        {/* Champions Table */}
        <ChampionsTable champions={champions} loading={loading} />
      </div>
    </div>
  )
}

// Composants auxiliaires
function LoadingSkeleton() {
  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-slate-100 flex items-center justify-center">
      <div className="text-center">
        <div className="w-16 h-16 border-4 border-blue-600 border-t-transparent rounded-full animate-spin mx-auto mb-4" />
        <p className="text-slate-900 text-xl">Loading player data...</p>
      </div>
    </div>
  )
}

function ErrorPage({ error }: { error: string }) {
  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-slate-100 flex items-center justify-center">
      <Card className="w-full max-w-md bg-white border-red-200 shadow-lg">
        <CardHeader>
          <CardTitle className="text-red-600">Error</CardTitle>
        </CardHeader>
        <CardContent>
          <p className="text-slate-700 mb-4">{error}</p>
          <Button asChild className="w-full bg-blue-600 hover:bg-blue-700 text-white">
            <Link href="/">Back to Home</Link>
          </Button>
        </CardContent>
      </Card>
    </div>
  )
}

function RankCard({ title, rank }: { title: string, rank: string | null }) {
  if (!rank) {
    return (
      <div className="bg-slate-50 p-6 rounded-xl border border-slate-200">
        <div className="flex items-center space-x-4">
          <Image
            src="/images/unranked.webp"
            alt="Unranked"
            width={48}
            height={48}
            className="rounded-lg"
          />
          <div>
            <h3 className="text-lg font-semibold text-slate-900">{title}</h3>
            <p className="text-slate-600">Unranked</p>
          </div>
        </div>
      </div>
    )
  }

  const rankParts = rank.split(' ')
  const tier = rankParts[0]?.toLowerCase()
  const rankText = rankParts.slice(0, 2).join(' ')
  const lp = rankParts[2]?.replace('(', '') || ''
  const winrate = rankParts[7]?.split('%')[0] || '0'

  return (
    <div className="bg-slate-50 p-6 rounded-xl border border-slate-200">
      <div className="flex items-center space-x-4">
        <Image
          src={`/images/${tier}.webp`}
          alt={rankText}
          width={48}
          height={48}
          className="rounded-lg"
        />
        <div className="flex-1">
          <h3 className="text-lg font-semibold text-slate-900">{title}</h3>
          <p className="text-slate-900 font-medium">{rankText} {lp}</p>
          <div className="flex items-center space-x-2 mt-1">
            <span className="text-sm text-slate-600">{rankParts[5]}</span>
            <span className="text-sm text-green-600 font-medium">{winrate}%</span>
          </div>
          <Progress value={parseInt(winrate)} className="mt-2 h-2" />
        </div>
      </div>
    </div>
  )
}

function ChampionsTable({ champions, loading }: { champions: Champion[], loading: boolean }) {
  if (loading) {
    return (
      <div className="space-y-4">
        {[...Array(5)].map((_, i) => (
          <Skeleton key={i} className="h-16 w-full bg-slate-200" />
        ))}
      </div>
    )
  }

  const minGamesRequired = 4

  return (
    <Card className="bg-white border-slate-200 shadow-lg">
      <CardHeader>
        <CardTitle className="text-slate-900">Champion Performance</CardTitle>
        <CardDescription className="text-slate-600">
          Performance statistics for each champion played
        </CardDescription>
      </CardHeader>
      <CardContent>
        <Table>
          <TableHeader>
            <TableRow className="border-slate-200">
              <TableHead className="text-slate-700">Champion</TableHead>
              <TableHead className="text-slate-700">Games</TableHead>
              <TableHead className="text-slate-700">Winrate</TableHead>
              <TableHead className="text-slate-700">KDA</TableHead>
              <TableHead className="text-slate-700">KP</TableHead>
              <TableHead className="text-slate-700">Dangerousness</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {champions
              .filter(champ => champ.nombre_de_parties >= minGamesRequired)
              .sort((a, b) => b.dangerousness - a.dangerousness)
              .map((champion) => (
                <TableRow key={champion.nom} className="border-slate-200 hover:bg-slate-50">
                  <TableCell>
                    <div className="flex items-center space-x-3">
                      <Image
                        src={`/images/${champion.nom}.png`}
                        alt={champion.nom}
                        width={32}
                        height={32}
                        className="rounded-lg cursor-pointer hover:scale-110 transition-transform"
                        onClick={() => window.open(`https://u.gg/lol/champions/${champion.nom}/counter`, '_blank')}
                      />
                      <span className="font-medium text-slate-900">{champion.nom}</span>
                    </div>
                  </TableCell>
                  <TableCell className="text-slate-900">{champion.nombre_de_parties}</TableCell>
                  <TableCell>
                    <div className="flex items-center space-x-2">
                      <span className={`font-medium ${
                        champion.winrate >= 70 ? 'text-green-600' : 
                        champion.winrate >= 50 ? 'text-yellow-600' : 'text-red-600'
                      }`}>
                        {champion.winrate}%
                      </span>
                      <span className="text-sm text-slate-500">
                        ({champion.nombre_win}W/{champion.nombre_lose}L)
                      </span>
                    </div>
                  </TableCell>
                  <TableCell>
                    <div className="flex items-center space-x-2">
                      <span className={`font-medium ${
                        champion.kda >= 4 ? 'text-green-600' : 
                        champion.kda >= 3 ? 'text-yellow-600' : 'text-red-600'
                      }`}>
                        {champion.kda}
                      </span>
                      <span className="text-sm text-slate-500">
                        ({champion.kill}/{champion.death}/{champion.assit})
                      </span>
                    </div>
                  </TableCell>
                  <TableCell>
                    <span className={`font-medium ${
                      champion.kill_participation >= 65 ? 'text-green-600' : 
                      champion.kill_participation >= 50 ? 'text-yellow-600' : 'text-red-600'
                    }`}>
                      {champion.kill_participation}%
                    </span>
                  </TableCell>
                  <TableCell>
                    <Badge 
                      variant={champion.dangerousness >= 700 ? 'destructive' : 'secondary'}
                      className="font-medium"
                    >
                      {champion.dangerousness}
                    </Badge>
                  </TableCell>
                </TableRow>
              ))}
          </TableBody>
        </Table>
      </CardContent>
    </Card>
  )
}